<template>
  <section id="balance">
      <span class="title">{{$t('navBar.balance')}}</span>
      <div>
      	<div class="kuai">
      		<p class="center  count">{{assetBalanceData.goldBanance}}</p>
      		<p class="center explain">{{$t('goldStock')+"(g)"}}</p>
      	</div>
      	<div class="kuai">
      		<p class="center count">{{assetBalanceData.marketUsdgBalance}}</p>
      		<p class="center explain">{{$t('marketFloat')}}</p>
      	</div>
      	<div class="kuai">
      		<p class="center count">{{assetBalanceData.usdgBalance}}</p>
      		<p class="center explain">{{$t('USDGrepertory')}}</p>
      	</div>
      	<div class="addKu" @click="showAddGold = true">{{$t('stockpiling')}}</div>
      </div>
      <div class="bottom">
      	<div class="kuai">
      		<p class="center count">{{assetBalanceData.btyBalance}}</p>
      		<p class="center explain">{{$t('BTYcount')}}</p>
      	</div>
      	<input type="button" @click="queryPlatformAddress" :value="$t('assets.operate[1]')" />
      	<input type="button" @click="tiBi = true" :value="$t('assets.operate[2]')" />
      	<input type="button" @click="queryAddresses" :value="$t('assets.operate[3]')" />
      </div>
		<!-- 点击充币出现的模态框 -->

		<el-dialog :title="$t('assets.operate[1]')" :visible.sync="chongBi" width="700" >
	  		<el-row>
			  	<el-col :offset="1":span="4"><div id="qrcode"></div></el-col>
			  	<el-col :offset="1":span="17">
			  		<div class="coinTi">
		  				<p>{{$t('plaese')}} <el-button class='btn' v-clipboard:copy="chongBiData" v-clipboard:success="onCopy" v-clipboard:error="onError">{{$t('copy')}}</el-button></p>
		  			 	<h4>{{chongBiData}}</h4>
	  				</div></el-col>
			</el-row>
		  	<el-row>
			  	<el-col :offset="1":span="22"><p class="hint">{{$t('prompt')}}</p></el-col>
		  	</el-row>
		  	<el-row>
			  	<el-col :offset="1":span="22"><p class="hintDetails">{{$t('Pcontent')}}</p></el-col>
		  	</el-row>
		</el-dialog>

     <!-- 提币弹框 -->

		<el-dialog :title="$t('assets.operate[2]')" id="TBTK" :visible.sync="tiBi" width="700">
			 <el-form :model="tiBiData">
				    <el-form-item  id="tibidizhi" :label="$t('TBaddress')" :label-width="formLabelWidth">
				      	<el-select v-model="select"   :placeholder="$t('PTBaddress')">
							<el-option  v-for="item in addressData" :label="item.address" :value="item.address" :key="item.id">
							</el-option>
						</el-select>

				      <el-button @click="addAddress = true" class="two addTow "><i class="iconfont icon-jiahao"></i> {{$t('addAddress')}}</el-button>
				    </el-form-item>
				    <el-form-item :label="$t('TBcount')" :label-width="formLabelWidth">
				      <el-input v-model="tiBiData.amount" auto-complete="off" @keyup.native="TBcount"></el-input>
				      <span class="two top">{{$t('extract')}}{{assetBalanceData.btyBalance}}BTY</span>
				    </el-form-item>
				    <el-form-item :label="$t('phone')" :label-width="formLabelWidth">
            		  <el-input :placeholder="$t('Pphone')"  v-model="tiBiData.phone" :disabled="tiBiData.phone !=''" class="input-with-select">
					    <template slot="prepend">+86</template>
					  </el-input>
				    </el-form-item>
				    <el-form-item :label="$t('validate')" :label-width="formLabelWidth">
				      <el-input v-model="tiBiData.code" :placeholder="$t('Pvalidata')" class="three" auto-complete="off"></el-input>
				      <!-- <el-button @click="sendVerificationCode" class="verCode">{{$t('Gvalidata')}}</el-button> -->
				      <el-button @click="sendVerificationCode" class="verCode">{{yanZh}}</el-button>

				    </el-form-item>
				    <el-form-item label="" :label-width="formLabelWidth">
				    	<el-button @click="withdrawBTY" type="warning">{{$t('QRTB')}}</el-button>
				    </el-form-item>
  			 </el-form>
		   	<el-row>
			  	<el-col :offset="1":span="22"><p class="hint">{{$t('prompt')}}</p></el-col>
		  	</el-row>
		  	<el-row>
			  	<el-col :offset="1":span="22"><p class="hintDetails">{{$t('Pcontent')}}</p></el-col>
		  	</el-row>
		</el-dialog>

		 <!-- 地址管理 -->
		 <div id="addressAdministration">
		<el-dialog :title="$t('assets.operate[3]')" id="adressFind" :visible.sync="diHziGuanLi" width="700">

			<el-table :data="addressData">
			    <el-table-column property="tag" :label="$t('assets.theadPop[0]')" width="150"></el-table-column>
			    <el-table-column property="address" :label="$t('assets.theadPop[1]')" width="300"></el-table-column>
			    <el-table-column :label="$t('assets.theadPop[2]')" width="100">
				      <template slot-scope="scope">
				        <el-button type="text" size="small" @click="open2(scope.$index,scope.row,deleteAddress)">{{$t('delete')}}</el-button>
				      </template>
				</el-table-column>
			    <el-table-column property="default" :label="$t('default')">
			    	<template slot-scope="scope">
			    		 <el-checkbox v-model ="addressData[scope.$index].id ==checked || addressData[scope.$index].isDefault==1"  @change="setDefaultAddress(scope.$index,scope.row)" label="" ></el-checkbox>
			    	</template>
			    </el-table-column>
		    </el-table>

		    <div style="text-align:center;">
		    	<button @click="addAddress = true ,diHziGuanLi=false;" class="btn">{{$t('assets.add')}}</button>
			</div>

		</el-dialog>
		</div>
		<!-- 添加地址 -->
			
		<el-dialog :title="$t('assets.add')" :visible.sync="addAddress">
			<el-form :model="addAddressDate">
			    <el-form-item :label="$t('TBaddress')" :label-width="formLabelWidth">
			      <el-input v-model="addAddressDate.address" auto-complete="off"></el-input>
			    </el-form-item>
			    <el-form-item :label="$t('assets.addressTag')" :label-width="formLabelWidth">
			      <el-input v-model="addAddressDate.tag" auto-complete="off"></el-input>
			    </el-form-item>
		    </el-form>

 		 	<div style="text-align:center;margin-bottom:15px;">
				<button class="btn" @click="addAddressFn">{{$t('assets.newDo')}}</button>
			</div>
  			
  			<el-table :data="addressData">
			    <el-table-column property="tag" :label="$t('assets.theadPop[0]')" width="150"></el-table-column>
			    <el-table-column property="address" :label="$t('assets.theadPop[1]')" width="330"></el-table-column>
			    <!-- <el-table-column property="do" label="$t('assets.theadPop[0]')"></el-table-column> -->
			    <el-table-column
  					:label="$t('assets.theadPop[2]')"
  					width="100">
				      <template slot-scope="scope">
				        <!-- <el-button type="text" size="small" @click="deleteAddress(scope.$index,scope.row)">{{$t('delete')}}</el-button> -->
				        <el-button type="text" size="small" @click="open2(scope.$index,scope.row,deleteAddress)">{{$t('delete')}}</el-button>
				      </template>
				</el-table-column>
				<el-table-column property="default" :label="$t('default')">
			    	<template slot-scope="scope">
			    		 <el-checkbox v-model =" addressData[scope.$index].id == checked ||  addressData[scope.$index].isDefault==1"   @change="setDefaultAddress(scope.$index,scope.row)" label="" ></el-checkbox>
			    	</template>
			    </el-table-column>
		    </el-table>
		</el-dialog>

		<!-- 增加库存黄金 -->
		<div id="addCGold">
		<el-dialog :title="$t('stockpilingGold')" :visible.sync="showAddGold" id="goldPop" center >
			<el-form :model="form" class="formCenter">
			    <el-form-item :label="$t('GoldProduction')" :label-width="formLabelWidth">
			      <el-input v-model="form.count" auto-complete="off"></el-input><span class="cook">kg</span>
			    </el-form-item>
			</el-form>
		    <div slot="footer" class="dialog-footer">
		      <el-button @click="showAddGold = false" class="exit">{{$t('cancel')}}</el-button>
		      <el-button type="primary" @click="addGold" class="sureSure">{{$t('confirm')}}</el-button>
		    </div>
	  	</el-dialog>
	  	</div>
  	</section>
</template>

<script>
import {ajax,dateStr2GmtTimeStamp,getDataYear} from '../../common/js/common.js'
import QRCode from 'qrcodejs2' 

export default {
	components:{QRCode},
	data(){
		return {
			showAddGold:false,
			sureShowAddGold:false,
			form:{
				count:"",
			},
			yanZh:"获取验证码",
			// copyContent:"BTY充币需要一个确认,实际到账时间取决于区块链确认速度,大约需要5~30分钟时间",
			select:"",
			selectPhone:"",
			checked:"",
			chongBi: false,
			chongBiData:"",
			tiBi: false,
			tiBiData:{
				toAddress:"",
				amount:"",
				code:"",
				fee:0,
				phone:"",
				coinType:'2',
			},
			diHziGuanLi: false,
			addAddress:false,
			addressData: [],
       		addAddressDate:{
        			address:"",
        			tag:""
    				},
        	formLabelWidth: '90px',
        	assetBalanceData:{
		        	goldBanance:0,
		        	btyBalance:0,
		        	marketUsdgBalance:0,
		        	totalUsdgAmount:0,
		        	usdgBalance:0,
		        	},
		}
	},
	
	methods:{
		// 提币数量
		TBcount:function(){
			if(parseInt(this.tiBiData.amount)<0 || parseInt(this.tiBiData.amount)>parseInt(this.assetBalanceData.btyBalance)){
				this.$notify({
					title:"提示",
					message:"请输入合理的数量",
					type:"warning"
				})
				return false;
			}else{
				return ;
			}
			// this.assetBalanceData.btyBalance
		},
		// 有关复制
		onCopy: function (e) {
		      console.log('你刚刚复制: ' + e.text)
		    },
    	onError: function (e) {
		      console.log('无法复制文本！')
		    },
		// 生成二维码
	    qrcode () {  
		      let qrcode = new QRCode('qrcode', {  
		        width: 90,  
		        height: 90, // 高度  
		        text: this.chongBiData, // 二维码内容  
		        // render: 'canvas' // 设置渲染方式（有两种方式 table和canvas，默认是canvas）  
		        // background: '#f0f'  
		        // foreground: '#ff0'  
		      })  
		    //   console.log(qrcode)  
	    },
		// 查询平台的资产余额
		queryAssetBalance:function(){
			ajax(this,this.ApiConfig.assetManagement.queryAssetBalance,{},(res) =>{
				if(res.code == 200){
					// console.log(res)
				this.assetBalanceData = res.data;
			}else{
				this.$notify({
					title:"提示",
					message:res.message,
					type:"warning"
				})
				return false;
			}
			},"POST")
		},
		// 用户点击充币显示平台BTY地址
		queryPlatformAddress:function(){
			this.chongBi = true;
			ajax(this,this.ApiConfig.assetManagement.queryPlatformAddress,null,(res) =>{
				if(res.code==200){
					// console.log(res)
					if(this.chongBiData == res.data){
						return ;
					}else{
						this.chongBiData=res.data;
					 	this.qrcode() ;
					}
				}else{
					this.$notify({
						title:"提示",
						message:res.message,
						type:"warning"
					})
					return false;
				}
			},"POST")
		},	
		// 发送短信验证码(通过手机号)
		
		sendVerificationCode(){
			
			let that =this;
			let  phone =this.tiBiData.phone;
			console.log(phone)
			let formData = new FormData();
			formData.append("phone",phone);
			this.axios.post(this.ApiConfig.backstageSM.sendVerificationCode,formData,{
				headers:{
					'Content-Type':'multipart/form-data;charset=UTF-8',
					Authorization:sessionStorage.getItem('tokenId')
				}
			}).then(function(res){
				// console.log(res.data)
				if(res.data.code == 200){
					// console.log(res.data)
					let n=60;
				var timer =	setInterval(function(){ 
						 
						n--;
						 that.yanZh = n + "s重新获取验证码"; 
						 if(n==0){
						 	that.yanZh ="获取验证码";
						 	clearInterval(timer)
						 }
					}, 1000);
				}else{
					that.$notify({
						title:"提示",
						message:res.data.message,
						type:"warning"
					})
					return false;
				}

			}).catch(function(error){
				console.log(error)
			});
			
		},
		// 资产提币(平台提取BTY)
		withdrawBTY:function(){
			
			this.tiBiData.toAddress = this.select;
			console.log("参数错误?")
			console.log(this.tiBiData)
			ajax(this,this.ApiConfig.assetManagement.withdrawBTY,this.tiBiData,(res) =>{
				if(res.code==200){

					console.log(res)
					this.$notify({
						title:"提示",
						message:res.message,
						type:"success"
					})
					this.tiBi = false;
				}else{
					this.$notify({
						title:"提示",
						message:res.message,
						type:"warning"
					})
					return false;
				}
			},"POST")
		},
		// 点击地址管理显示平台提币管理的所有地址
		queryAddresses:function(){
	 	    this.diHziGuanLi = true

			ajax(this,this.ApiConfig.assetManagement.queryAddresses,{},(res) => {
				// console.log(res)
				this.addressData =res.data;
				// console.log(this.addressData)
			},'POST')
		},
		open2(index,row,fn) {
        this.$confirm('此操作将永久删除该地址, 是否继续?', '提示', {
          confirmButtonText: '确定',
          cancelButtonText: '取消',
          type: 'warning'
        }).then(() => {
          this.$message({
            type: 'success',
			message: '删除成功!',
			callback:fn(index,row)
          });
        }).catch(() => {
          this.$message({
            type: 'info',
            message: '已取消删除'
          });          
        });
      },
		//删除管理的地址
		deleteAddress:function(index,row){
			let data = {
				id:row.id
			};
			ajax(this,this.ApiConfig.assetManagement.deleteAddress,data,(res) =>{
				if(res.code==200){
					this.$notify({
					title:'提示',
					message:res.message,
					type:"success"
				})
					// console.log(res);
				this.queryAddresses();
				
			}else{
				this.$notify({
					title:'提示',
					message:res.message,
					type:"warning"
				})
				return false;
			}
				
			},"POST")
			
		},
		// 添加管理的地址
		addAddressFn:function(){
            
            ajax(this,this.ApiConfig.assetManagement.addAddress,this.addAddressDate,(res)=>{
            	if(res.code ==200){
					this.$notify({
            			title:"提示",
            			message:res.message,
            			type:"success"
            		})
            		// console.log(res)
            		this.addAddressDate.address="";
					this.addAddressDate.tag="";
					this.addAddress=false;
            		this.queryAddresses();
            	}else{
            		this.$notify({
            			title:"提示",
            			message:res.message,
            			type:"warning"
            		})
            		return false;
            	}
            },"POST")
		},
		setDefaultAddress:function(index,row){
			// console.log(row)
			this.checked =row.id;
			let data={
				id:row.id
			};
			ajax(this,this.ApiConfig.assetManagement.setDefaultAddress,data,(res)=>{
				if(res.code ==200){
					// console.log(res)
					let arr = this.addressData;
					arr.forEach((item,i,arr) =>{
						arr[i].isDefault=0;
					})
					this.queryAddresses();
				}else{
					this.$notify({
						title:"提示",
						message:res.message,
						type:"warning"
					})
					return false;
				}
			},"POST")
		},

		// 平台增加黄金
		addGold:function(){
			/*console.log(this.$store.state.lang)
			console.log(typeof(this.$store.state.lang))*/
				 let data = {gold:this.form.count};
			ajax(this,this.ApiConfig.assetManagement.addUsdg,data,(res)=>{
				if(res.code ==200){
					// console.log(res)
					this.$notify({
						title:"提示",
						message:res.message,
						type:"success"
					})
					this.showAddGold =false;
					this.queryAssetBalance();
				}else{
					this.$notify({
						title:"提示",
						message:res.message,
						type:"warning"
					})
					return false;
				}
			},"POST")
		}
	},
	mounted:function(){
		this.queryAssetBalance();
		this.queryAddresses();
		this.diHziGuanLi = false;
		// console.log("这个是登陆者的信息")
		// console.log(sessionStorage.getItem("userPhone"))
		this.tiBiData.phone=sessionStorage.getItem("userPhone")
		
	}	
}
</script>

