			border-radius: 4px;
		}



	}

	#goldPop{

		.el-input{
			width: 200px;
		}
		.cook{
			position: absolute;
			left: 170px;
		}
	}
	
	.icon-jiahao{
		font-size: 12px;
	}
	.formCenter{
		width: 300px;
		margin: 0 auto;

	}

