	}

</style>
<style>
	/*  header .headNav .main .content-container[data-v-dfb4a462]{
		min-width: 1575px;
	} */

#balance{	
	.el-form-item {
    margin-bottom: 5px;
		}
	.el-button--warning {
		/* background-color: #bf9764; */

	width: 390px;
	height: 34px;
	background-image: linear-gradient(
		#bf9764, 
		#bf9764), 
	linear-gradient(
		#bf9764, 
		#bf9764);
	background-blend-mode: normal, 
		normal;
	border-radius: 4px;


	}
	.el-dialog{
		width: 540px;
		border-radius: 8px;
	}
	#addCGold {
